.tweets{
    display: flex;
    border: 1px solid #D8D8D8;
    border-top: none;
    padding: 10px 20px;
    &__img{
        height: 60px;
    }
    &__info-wrapper{
        margin-left: 15px;
    }
    &__info{
        display: flex;
        align-items: center;
        margin-bottom: 5px;
        &-btn{
            border: none;
            background-color: transparent;
            margin-left: auto;
            cursor: pointer;
        }
    }
    &__name{
        font-size: 20px;
        line-height: 27px;
        margin-right: 5px;
    }
    &__username, &__time{
        font-size: 18px;
        line-height: 24px;
        opacity: .6;
    }
    &__text{
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 22px;
    }
    &__btns{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 80%;
    }
    &__btn-wrapper{
        display: flex;
        align-items: center;
    }
    &__btn{
        background-color: transparent;
        border: none;
        cursor: pointer;
        margin-right: 10px;
    }
}