.nav{
    position: fixed;
    background-color: #fff;
    padding-top: 20px;
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 18%;

    &__logo{
        margin-bottom: 40px;
    }
    &__link{
        display: flex;
        align-items: center;
        font-weight: 500;
        font-size: 18px;
        line-height: 24px;
        color: #000;
        margin-bottom: 25px;
        img{
            margin-right: 20px;
        }
    }
    &__more-btn{
        font-weight: 500;
        font-size: 18px;
        line-height: 24px;
        color: #000;
        margin-bottom: 25px;
        display: flex;
        align-items: center;
        background-color: transparent;
        border: none;
        cursor: pointer;
        img{
            margin-right: 20px;
        }
    }
    &__btn{
        font-weight: bold;
        font-size: 18px;
        line-height: 24px;
        color: #fff;
        background: #1D9BF0;
        border-radius: 27.5px;
        width: 87%;
        border: none;
        padding: 15px 0;
        cursor: pointer;
    }
    &__account{
        margin-top: auto;
        margin-bottom: 15px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        &-img{
            margin-right: 10px;
        }
        &-name{
            font-weight: 600;
            font-size: 16px;
            line-height: 21px;
            margin-bottom: 2px;
        }
        &-username{
            font-size: 16px;
            line-height: 21px;
            font-weight: normal;
            color: #000000;
            opacity: 0.6;
        }
    }
}

.active{
    font-weight: bold;
}