.form{
    max-width: 450px;
    margin: 0 auto;
    margin-top: 60px;
    &__title{
        font-weight: 900;
        font-size: 42px;
        line-height: 49px;
        margin: 36px 0;
    }
    &__input{
        display: block;
        width: 100%;
        margin-bottom: 25px;
        font-size: 18px;
        border: 1px solid rgba(0, 0, 0, 0.2);
        border-radius: 6px;
        padding: 22px;
        font-weight: normal;
        &:focus{
            outline: none;
        }
    }
    &__btn{
        width: 100%;
        margin-bottom: 40px;
        background: #1DA1F2;
        border-radius: 76px;
        border: none;
        font-weight: bold;
        color: #fff;
        font-size: 18px;
        line-height: 24px;
        padding: 18px 0;
        cursor: pointer;
    }
    &__links{
        display: flex;
        justify-content: space-between;
        a{
            font-size: 18px;
            line-height: 24px;
            color: #1DA1F2;
        }
    }
}