.right{
    width: 24%;
    padding-top: 10px;
    margin-left: 25px;
    &__search-wrapper{
        position: relative;
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        img{
            position: absolute;
            left: 20px;
        }
    }
    &__search{
        width: 100%;
        font-size: 18px;
        line-height: 24px;
        color: #5C6C79;
        background: #EFF3F4;
        border-radius: 31px;
        padding: 15px 60px;
        border: none;
        &:focus{
            outline: none;
        }
    }
    &__trends{
        background: #F7F9F9;
        border-radius: 10px;
        padding: 15px 25px;
        margin-bottom: 20px;
        &-header{
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 20px;
        }
        &-title{
            font-size: 24px;
            line-height: 32px;
        }
        &-btn{
            margin: 15px 0;
            color: #1DA1F2;
            font-size: 18px;
            background-color: transparent;
            border: none;
        }
    }
    &__trend{
        display: flex;
        justify-content: space-between;
        margin-bottom: 15px;
        cursor: pointer;
        &-type, &-tweets{
            display: inline-block;
            font-size: 16px;
            line-height: 25px;
            color: #393B41;
            opacity: 0.6;
        }
        &-title{
            font-weight: 600;
            font-size: 20px;
            line-height: 27px;
            margin-bottom: 3px;
        }
        &-btn{
            background-color: transparent;
            border: none;
            transform: translateY(-20px);
            cursor: pointer;
        }
    }
    &__follow{
        background: #F7F9F9;
        border-radius: 10px;
        padding: 15px 25px;
        margin-bottom: 30px;
        &-title{
            font-size: 24px;
            line-height: 32px;
            margin-bottom: 25px;
        }
        &-link{
            color: #1DA1F2;
            font-size: 18px;
        }
    }
    &__account{
        margin-bottom: 25px;
        display: flex;
        align-items: center;
        &-info{
            margin-left: 13px;
        }
        &-name{
            font-size: 17px;
            line-height: 24px;
        }
        &-username{
            font-size: 17px;
            opacity: .6;
        }
        &-btn{
            font-size: 18px;
            margin-left: auto;
            border: none;
            background-color: #000;
            cursor: pointer;
            border-radius: 50px;
            padding: 10px 18px;
            color: #fff;
        }
    }
    &__bottom-links{
        a, button, span{
            display: inline-block;
            font-size: 16px;
            line-height: 21px;
            color: #536471;
            margin-right: 15px;
            background-color: transparent;
            border: none;
            margin-bottom: 10px;
        }
    }
}