.header{
    &__top{
        background-color: #fff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border: 1px solid #D8D8D8;
        border-top: none;
        padding: 20px;
        img{
            cursor: pointer;
        }
    }
    &__tweet{
        border: 1px solid #D8D8D8;
        border-top: none;
        padding: 10px 20px;
        &-top{
            display: flex;
            align-items: center;
            margin-bottom: 33px;
            input{
                border: none;
                color: #828282;
                font-weight: 500;
                font-size: 22px;
                line-height: 29px;
                margin-left: 15px;
                &:focus{
                    outline: none;
                }
            }
        }
        &-bottom{
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-left: 80px;
            .img-wrapper{
                display: flex;
                align-items: center;
                img{
                    margin-right: 20px;
                    cursor: pointer;
                }
            }
        }
        &-btn{
            font-weight: bold;
            font-size: 17px;
            color: #fff;
            background: #1DA1F2;
            opacity: 0.4;
            box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.08);
            border-radius: 120px;
            border: none;
            padding: 16px 30px;
            cursor: pointer;
        }
    }
}